@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
/* base styles */
html, body, #root {
  height: 100%;
}
body {
  font-family: Raleway, sans-serif;
  margin: 0;
  font-size: 1.4em;
  background: #dfdfdf;
  color: #333;
}
h1,h2,h3,p {
  margin: 0;
}
input, textarea {
  color: #333;
  padding: 8px;
  font-size: 1em;
  border-radius: 4px;
  border: 1px solid #d4d4d4;
  background-color: #fff;
  display: block;
  width: 100%;
  box-sizing: border-box;
}
.page-title {
  text-align: center;
  margin: 40px auto;
  color: #333;
}
.loading, .error {
  text-align: center;
  margin: 40px auto;
}
.App {
  min-height: 100%;
}
.navbar {
    background: lightpink;
    padding: 50px;
}

.navbar nav {
    display:flex
    

}

.navbar a.brand{
    margin-right: auto;
    color: white;
    -webkit-text-decoration: forestgreen;
            text-decoration: forestgreen;
}

.navbar a:last-child {
    color:black;
    text-decoration: none;
    margin-left:20px;
    border: 3px solid white;
    border-radius: 10px;


}



.home {
    max-width: 1200px;
    margin: 60px auto;
}
.recipe-list {
    display: grid;
    grid-template-columns:  1fr ;
    padding: 50px;
    max-width: 1200px;
    margin: 50px;

}

.recipe-list .card {
    background: white;
    color: black;
    padding: 50px;
    margin: 10px;
    border-radius: 10px;
    box-shadow: 3px 3px 3px green;
    transition: all 0.3s ease;
    position: relative;
}

.recipe-list .card:hover {
    -webkit-transform: roate(60deg);
            transform: roate(60deg);
}

.recipe-list .card h3 {
    color: gray;
    margin-bottom: 6px;
}

.recipe-list .card p {
    color: black;
    font-size: 0.9em
}

.recipe-list .card div {
    color: gray;
    font-size: 0.7em;
    margin: 20px 0;
    line-height: 1.5em;
}
.about {
    text-align: center;
    max-width: 1200;
    margin-bottom: 10px;
    
}

.programs .card {
    background: white;
    color: black;
    padding: 50px;
    margin: 10px;
    border-radius: 10px;
    box-shadow: 3px 3px 3px green;
    transition: all 0.3s ease;
    position: relative;

}
.about .heading {
    margin-left: 10px;
    padding: 3px;
}
